import React, { useState } from 'react';
import styles from './css/Initial.module.css';

import { Bar, Line } from 'react-chartjs-2';

import { connect } from 'react-redux';

function Result(props) {
	let total = 0;
	let marks = 0;
	props.questions.forEach((data) => {
		//Calculating marks
		total += data.fields.marks;
		marks += data.marks;
	});
	let average = [ 0, 0, 0, 0, 0, 0, 0 ];
	props.fer_data.forEach((data) => {
		average = average.map((avg, index) => avg + data.emotions[index]);
	});
	average = average.map((data) => data / props.fer_data.length);
	let Bar_Chart_Data = {
		labels: [ 'angry', 'disgust', 'fear', 'happy', 'sad', 'surprise', 'neutral' ],
		datasets: [
			{
				label: 'Average Emotions',
				data: average,
				backgroundColor: [ 'red', 'green', 'black', 'yellow', 'blue', 'brown', 'gray' ]
			}
		]
	};
	let Line_Chart_Data = {
		labels: props.fer_data.map((data, index) => index),
		// data.time.substring(0, data.time.length - 4)),
		datasets: [
			{
				data: props.fer_data.map((data) => data.emotions[0] * 100),
				label: 'Angry',
				borderColor: 'red'
			},
			{
				data: props.fer_data.map((data) => data.emotions[1] * 100),
				label: 'Disgust',
				borderColor: 'green'
			},
			{
				data: props.fer_data.map((data) => data.emotions[2] * 100),
				label: 'Fear',
				borderColor: 'black'
			},
			{
				data: props.fer_data.map((data) => data.emotions[3] * 100),
				label: 'Happy',
				borderColor: 'yellow'
			},
			{
				data: props.fer_data.map((data) => data.emotions[4] * 100),
				label: 'Sad',
				borderColor: 'blue'
			},
			{
				data: props.fer_data.map((data) => data.emotions[5] * 100),
				label: 'Surprise',
				borderColor: 'brown'
			}
		]
	};
	return (
		<div id={styles.entry} className="p-4 bg-light m-4">
			<h1 className="display-3 text-center">Results</h1>
			<h1 className="display-2 text-center">
				{marks}/{total}
			</h1>
			<button className="btn btn-primary d-block m-auto" onClick={() => props.enter(2)}>
				check answers
			</button>
			{props.fer_data.length != 0 ? (
				<React.Fragment>
					<h2 className="ml-2 mt-2">Report</h2>
					<hr className="m-0 p-0" />
					<h5 className="ml-3 mt-3">Expression Recognizer:</h5>

					<div style={{ width: '90%', height: '60%' }}>
						<Bar data={Bar_Chart_Data} options={{ maintainAspectRatio: false }} />
					</div>
					<div style={{ width: '100%', height: '100%' }} className="mt-4">
						<Line data={Line_Chart_Data} options={{ maintainAspectRatio: false }} />
					</div>
				</React.Fragment>
			) : (
				''
			)}
		</div>
	);
}

const mapStateToProps = (state) => {
	return {
		questions: state.Test.fields.questions,
		fer_data: state.Test.fields.fer_data
	};
};

export default connect(mapStateToProps)(Result);
